import React from "react";
import "./ViewBy.scss";
import {useSelector, useDispatch} from "react-redux";
import {setActiveView} from "../../TrackGiftsAction";

export const TRACK_GIFTS_VIEWS = [
  {
    "id": 1,
    "text": "Gifts"
  },
  {
    "id": 2,
    "text": "Gift Givers"
  }
]

const ViewBy = () => {
  const activeView = useSelector(state => state.trackGifts.activeView);
  const dispatch = useDispatch()

  const changeTrackGiftView = view => {
    dispatch(setActiveView(view))
  }

  return (
    <div className="track-gift-view-by">
      <p className="text-sm">View By</p>
      <div className="view-by-toggle-section mt-1">
        <div
          className={["view-by-toggle text-sm p-2", activeView.id === TRACK_GIFTS_VIEWS[0].id ? "active-view-toggle" : ""].join(" ")}
          onClick={() => changeTrackGiftView(TRACK_GIFTS_VIEWS[0])}
        >Gifts
        </div>
        <div
          className={["view-by-toggle text-sm p-2", activeView.id === TRACK_GIFTS_VIEWS[1].id ? "active-view-toggle" : ""].join(" ")}
          onClick={() => changeTrackGiftView(TRACK_GIFTS_VIEWS[1])}
        >Gift
          Givers
        </div>
      </div>
    </div>
  )
}

export default ViewBy;