const GET_TRACK_GIFTS = `
query registryGifts(
  $registryId: Int!,
  $fundsReceived: Boolean,
  $giftGivers: Boolean!,
  $after: String,
  $title_Icontains: String,
  $item_Price_Gte: Float,
  $item_Price_Lte: Float,
  $first: Int
){
  registryGifts(
    registryId:$registryId,
    fundsReceived: $fundsReceived,
    giftGivers: $giftGivers,
    after: $after,
    first: $first,
    title_Icontains: $title_Icontains,
    item_Price_Gte: $item_Price_Gte,
    item_Price_Lte: $item_Price_Lte
  ){
    edges{
      node{
        id,
        isGroupGift,
        featuredOrder,
        item{
          storeItemUrl,
          price
        },
        title,
        guestUser{
          guestEmail
          firstName
          lastName
          id
          contributionAmount
        },
        groupGift{
          id,
          amountReceived,
          guestUser{
            guestEmail
            firstName
            lastName
            id
            contributionAmount
          }
        }
      }
    }
    pageInfo{
      endCursor,
      hasNextPage
    }
  }
}
`

