import {TRACK_GIFTS_ACTION_TYPES} from "./TrackGiftsAction";
import {combineReducers} from "redux";
import {TRACK_GIFTS_VIEWS} from "./components/view-by/ViewBy";
import defaultData from "data/ReducerDefaultData.json";

// By default "Gifts" are selected
const activeView = (state = TRACK_GIFTS_VIEWS[0], action) => {
  switch (action.type) {
    case TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_ACTIVE_VIEW:
      return {...action.payload}
    default:
      return state
  }
}

const products = (state = [], action) => {
  switch (action.type) {
    case TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_SET_PRODUCT:
      return [...action.payload]
    default:
      return state
  }
}

const paginationInfo = (state = defaultData.trackGifts.pagination, action) => {
  switch (action.type) {
    case TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_SET_PAGINATION_INFO:
      return {...action.payload}
    default:
      return state
  }
}


export default combineReducers({
  activeView,
  products,
  paginationInfo
});