import React from "react";
import "./TogglePills.scss";

const TogglePills = (props) => {
    return (
        <div
          onClick={props.onClick}
          className={["cash-fund-toggle-pills px-2 py-1", props.className ? props.className : "", props.selectedClass ? props.selectedClass : ""].join(" ")}>
            <i className={["cash-fund-toggle-icon float-left", props.iconClass ? props.iconClass : null].join(" ")} />
            <span className="text-sm ml-2">{props.text}</span>
        </div>
    )
}

export default TogglePills;