import React, {Component} from 'react'
import {connect} from "react-redux";
import AddedCard from "modules/commons/components/added-card/AddedCard";
import {
  toggleMustHaveProduct,
  addRecommendedProduct,
  setFilterCategory,
} from "../../MyRegistryActions";
import CheckList from "modules/my-registry/components/checkList/CheckList";
import CheckListItems from "modules/my-registry/components/checkList/CheckListItems";
import ViewRecommend from 'modules/commons/components/view-recommend/ViewRecommend';
import FilterSection from '../../components/filter-section/FilterSection';
import {graphQL, getRecommendation} from 'api/axios';
import {GET_RECOMMENDATIONS} from 'api/queries';
import {TOGGLE_MUST_HAVE} from 'api/mutations';
import {getProducts, removeProductFromCartHandler, getCategories} from "utils/commonFunction";
import {Cookies} from "react-cookie";
import config from "api/config";
import axios from "axios";
import CashFundCard from "../../../commons/components/cash-fund-card/CashFundCard";
import Button from "../../../commons/components/button-component/ButtonComponent";
import AddCashFundModal from "../../../commons/components/cash-fund-card/add-cash-fund-modal/AddCashFundModal";

class Everything extends Component {

  state = {
    showCashFundModal: false,
    cashFundProduct: null
  }

  openCashFundModal = () => {
    this.setState({
      showCashFundModal: true
    })
  }

  setCashFundCategoryAndOpenModal = (product) => {
    this.setState({
      cashFundProduct: {...product}
    })
    this.openCashFundModal()
  }

  closeCashFundModal = () => {
    this.setState({
      showCashFundModal: false,
      cashFundProduct: null
    })
  }

  componentDidMount() {
    getCategories(this.props.stateRegistryInfo.id, this.props.dispatch);
    getProducts(this.props.stateRegistryInfo.id, this.props.dispatch);
  }


  // Updating product count in filter categories
  updateFilterList = (addedProducts) => {
    const newFilterList = [
      {
        id: "giftsAdded",
        display: "Gifts added",
        count: addedProducts.length,
        child: [
          {
            id: "mustHave",
            display: "Must have",
            count: addedProducts.filter(product => product.mostWanted).length
          },
          {
            id: "purchased",
            display: "Purchased",
            count: addedProducts.filter(product => product.isPurchased).length
          },
          {
            id: "reserved",
            display: "Reserved",
            count: addedProducts.filter(product => product.isReserved).length
          },
          {
            id: "available",
            display: "Available",
            count: 0
          }
        ]
      },
      {
        id: "giftsToAdd",
        display: "Gifts to add",
        count: 0
      }
    ]

    this.props.dispatch(setFilterCategory(newFilterList))
  }

  toggleMustHaveProduct = (product) => {
    let data = {
      query: TOGGLE_MUST_HAVE,
      variables: {
        productId: product.id,
        mustHaveValue: !product.mostWanted,
        quantity: product.quantity
      }
    }
    graphQL(data).then(res => {
      if (res.status === 200) {
        this.props.dispatch(toggleMustHaveProduct(product.item.id))
      }
    }).catch(err => {
      console.error(err.message)
    })
  }


  setAllRecommendation = async (categories) => {
    let recommendedCategory = []
    for (let parentCat of categories) {
      for (let category of parentCat.subCategories) {
        recommendedCategory.push({
          ...category,
          parentCategory: parentCat.categoryName,
        })
      }
    }
    for (let recommendedCat of recommendedCategory) {
      // for (let i = 0; i <= 5; i++) {
      // let recommendedCat = recommendedCategory[i]
      let data = {
        query: GET_RECOMMENDATIONS,
        variables: {
          productCount: 15,
          productType: "product_type:" + recommendedCat.categoryName
        }
      }
      const apiURL = process.env.NODE_ENV === 'production' ? config.PRODUCTION_API_URL : config.LOCAL_API_URL;
      const cookies = new Cookies();

      await axios.get(apiURL + 'items', {
        params: data,
        headers: {"Authorization": cookies.get("access_token") ? "Token " + cookies.get("access_token") : ""}
      })
        .then(res => {
          let rData = res.data.data.products.edges.map(item => {
            let resData = {...item.node}
            resData['quantity'] = 1
            resData['category'] = recommendedCat
            return resData
          })
          // count = count + 1;
          if (rData.length !== 0) {
            this.props.dispatch(addRecommendedProduct(rData))
          }
        }).catch(err => {
          console.error(err.message)
        })


      // getRecommendation({ params: data }).then(res => {
      //   let rData = res.data.data.products.edges.map(item => {
      //     let resData = { ...item.node }
      //     resData['quantity'] = 1
      //     resData['category'] = recommendedCat
      //     return resData
      //   })
      //   // count = count + 1;
      //   this.props.dispatch(addRecommendedProduct(rData))
      // }).catch(err => {
      //   console.error(err.message)
      // })
      //   )
      // ))
      // count = count + 1;
      // }
      // else {
      //   break;
      // }
    }
  }

  getSortedProducts = (addedProducts, stateCategories, stateRecommendedProducts, stateFilterApplied) => {
    const cashFundCategory = stateCategories.find(category => category.categoryType && Number(category.categoryType.id) === 2)
    let cashFundCount = 0
    if (cashFundCategory) {
      let productsToAppend = []
      for (let subCategory of cashFundCategory.subCategories) {
        if (Number(subCategory.addedItemsCount) === 0) {
          cashFundCount++;
          productsToAppend.push({
            item: {
              imageUrl: ""
            },
            registryCategories: [{
              id: subCategory.id,
              category: {
                ...subCategory
              }
            }]
          })
        }
      }
      addedProducts = [...addedProducts, ...productsToAppend]
    }

    let allProducts = []
    let productCount = 0;
    let categories = []
    for (let parentCat of stateCategories) {
      if (!parentCat.isParent) {
        categories.push({
          id: parentCat.id,
          name: parentCat.categoryName,
          added: parentCat.addedItemsCount,
          total: parentCat.totalItemsCount,
          parentCategory: {
            id: parentCat.id,
            name: parentCat.categoryName
          },
          categoryType: parentCat.categoryType,
        })
      } else {
        for (let category of parentCat.subCategories) {
          if (category.subProductTypes.length) {
            for (let subProduct of category.subProductTypes) {
              categories.push({
                id: subProduct.id,
                name: subProduct.categoryName,
                added: subProduct.addedItemsCount,
                total: subProduct.totalItemsCount,
                parentCategory: {
                  id: parentCat.id,
                  name: parentCat.categoryName
                },
                categoryType: category.categoryType,
              })
            }
          } else {
            categories.push({
              id: category.id,
              name: category.categoryName,
              added: category.addedItemsCount,
              total: category.totalItemsCount,
              parentCategory: {
                id: parentCat.id,
                name: parentCat.categoryName
              },
              categoryType: category.categoryType,
            })
          }
        }
      }
    }


    for (let category of categories) {
      let products = []
      let recommendations = []

      // Getting filtered Products and recommendations
      if (stateFilterApplied.parentFilter) {
        if (stateFilterApplied.parentFilter === "giftsAdded") {
          if (stateFilterApplied.childFilter.length > 0) {
            const childFilterArray = stateFilterApplied.childFilter

            for (let product of addedProducts) {
              let categoryFound = false
              for (let cat of product.registryCategories) {
                if (cat.id === category.id) {
                  categoryFound = true
                  break
                }
              }
              if (
                ((childFilterArray.includes("mustHave") && product.mostWanted) ||
                  (childFilterArray.includes("purchased") && product.isPurchased) ||
                  // TODO: change category
                  (childFilterArray.includes("reserved") && product.isReserved)) && categoryFound
              ) {
                products.push(product)
                // filteredProductsId.push(product.id)
              }
            }
          } else {
            // TODO: change category
            products.push(...addedProducts.filter(el => {
              let categoryFound = false
              for (let cat of el.registryCategories) {
                if (cat.id === category.id) {
                  categoryFound = true
                  break
                }
              }
              return categoryFound;
            }))
          }
        } else if (stateFilterApplied.parentFilter === "giftsToAdd") {
          let addedRecommendations = []
          recommendations = stateRecommendedProducts.filter(el => {
            if (el.category.id === category.id && !addedRecommendations.includes(el.id)) {
              addedRecommendations.push(el.id)
              return true;
            }
            return false;
          })
        }
      } else {
        // TODO: Change this condition(el.item.category.length)
        // TODO: change category
        products.push(...addedProducts.filter(el => {
          let categoryFound = false
          for (let cat of el.registryCategories) {
            if (cat.id === category.id) {
              categoryFound = true
              break
            }
          }
          return categoryFound && el.registryCategories.length
        }))
        let addedRecommendations = []
        recommendations = stateRecommendedProducts.filter(el => {
          if (el.category.id === category.id && !addedRecommendations.includes(el.id)) {
            addedRecommendations.push(el.id)
            return true;
          }
          return false;
        })
      }
      productCount += products.length;
      allProducts.push({
        category: category,
        products: products,
        recommendations: (category.added < category.total || category.total === 0) ? recommendations : []
      })
    }

    // Grouping products based on parent category
    let groupedProducts = []
    for (const product of allProducts) {
      let foundCategory = false;
      groupedProducts = groupedProducts.map(el => {
        if (el.id === product.category.parentCategory.id) {
          foundCategory = true;
          el.allProducts.push(product);
        }
        return el;
      })

      if (!foundCategory) {
        groupedProducts.push({
          ...product.category.parentCategory,
          allProducts: [product]
        })
      }
    }

    // Adding total products and recommendations
    groupedProducts = groupedProducts.map(el => {
      let productCount = 0;
      let recommendationCount = 0;

      for (let product of el.allProducts) {
        productCount += product.products.length;
        recommendationCount += product.recommendations.length;
      }

      el['totalProducts'] = productCount;
      el['totalRecommendations'] = recommendationCount;
      return el;
    })

    // Removing items that does not have products or recommendations based on filters
    if (stateFilterApplied.parentFilter) {
      if (stateFilterApplied.parentFilter === "giftsAdded") {
        groupedProducts = groupedProducts.filter(product => product.totalProducts > 0)
      } else if (stateFilterApplied.parentFilter === "giftsToAdd") {
        groupedProducts = groupedProducts.filter(product => product.totalRecommendations > 0)
      }
    } else {
      groupedProducts = groupedProducts.filter(product => (product.totalProducts > 0 || product.totalRecommendations > 0))
    }

    productCount -= cashFundCount
    return {groupedProducts, productCount};
  }

  getChecklistPercentage = (checklistItems) => {
    let totalItems = 0;
    let addedItems = 0;
    for (let category of checklistItems) {
      totalItems += category.totalItemsCount;
      addedItems += category.addedItemsCount;
    }

    return (addedItems / totalItems) * 100;
  }


  render() {
    if (this.props.stateRecommendedProducts.length === 0 && this.props.stateCheckList.length > 0) {
      this.setAllRecommendation(this.props.stateCheckList);
    }
    this.updateFilterList(this.props.stateAddedProducts)
    let {groupedProducts, productCount} = this.getSortedProducts(this.props.stateAddedProducts, this.props.stateCheckList, this.props.stateRecommendedProducts, this.props.stateFilterApplied)

    return (
      <>
        <div className="container">
          <div className="row mt-6">
            {/* View CheckList */}
            <div className="col-3">
              <CheckList percentage={this.getChecklistPercentage(this.props.stateCheckList)}/>
              {this.props.stateCheckList.map(item => <CheckListItems key={item.categoryName} item={item}
                                                                     dispatch={this.props.dispatch}/>)}
            </div>
            <div className="col-9">

              {/* Top Filter Section */}
              <FilterSection productCount={productCount}/>
              <div className="mt-5">
                {groupedProducts.map(el => {
                  let hasCashFund = false;
                  return (<>
                      <h4 className="text-uppercase my-4">{el.name}</h4>
                      {el.allProducts.map((product, i) => (
                        <div id={product.category.name}>
                          {product.products.map((card, index) => {
                            if (Number(card.registryCategories[0].category.categoryType.id) === 2) {
                              hasCashFund = true
                            }
                            if (Number(card.registryCategories[0].category.categoryType.id) === 2 && Number(card.registryCategories[0].category.addedItemsCount) === 0) {
                              return (
                                <CashFundCard
                                  data={card}
                                  key={"cashFund" + index}
                                  className="mt-2"
                                  addButtonClickHandler={() => this.setCashFundCategoryAndOpenModal(card)}
                                />
                              )
                            } else {
                              return (
                                <AddedCard
                                  data={card} key={"addedCard" + index}
                                  multipleCategories = {card.registryCategories.length > 1 ? card.registryCategories.find((cat, index) => Number(cat.id) === Number(product.category.id) && index != 0) : null}
                                  toggleMustHave={() => this.toggleMustHaveProduct(card)}
                                  removeProductFromCartHandler={() => removeProductFromCartHandler(card, this.props.stateRegistryInfo.id, this.props.dispatch)}
                                />
                              )
                            }
                          })}
                          {product.recommendations.length > 0 ?
                            <ViewRecommend
                              title={product.recommendations[0].category.categoryName}
                              category={product.recommendations[0].category}
                              products={product.recommendations}/>
                            : null}
                        </div>
                      ))}
                      {hasCashFund ?
                        <div className="my-2">
                          <Button
                            variant="secondary"
                            className=" w-100 py-1 text-body font-medium"
                            onClick={this.openCashFundModal}
                          >Add my own</Button>
                        </div>
                        : null}
                    </>
                  )
                })}
              </div>
            </div>
          </div>
        </div>
        {this.state.showCashFundModal ?
          <AddCashFundModal
            show={this.state.showCashFundModal}
            closeModal={this.closeCashFundModal}
            categoryData={this.state.cashFundProduct}
          />
          : null}
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    stateProducts: state.myRegistry.products,
    stateAddedProducts: state.myRegistry.addedProducts,
    stateFilterApplied: state.myRegistry.filterApplied,
    stateActiveRecommandation: state.myRegistry.activeRecommandation,
    stateRecommendedProducts: state.myRegistry.recommendedProducts,
    stateCheckList: state.myRegistry.checkList,
    stateRegistryInfo: state.registry.info
  }
}


export default connect(
  mapStateToProps
)(Everything);
