import React from "react";
import "./CashFundCardNotAdded.scss";

class CashFundCardNotAdded extends React.Component {
  render() {
    return (
      <div
        className={["baby-registry-cash-fund-card py-3 mb-2", this.props.className ? this.props.className : ""].join(" ")}>
        <div className="row">
          <div className="col-2">

            <div className="pl-3">
              {this.props.data.item.imageUrl ?
                <img className="cash-fund-image d-block mx-auto" src={this.props.data.item.imageUrl}/>
                :
                <div className="baby-registry-cash-fund-no-image" />
              }
            </div>

          </div>
          <div className="col">
            <div className="float-left">
              <p className="text-body font-medium">{this.props.data.registryCategories[0].category.categoryName}</p>
              <p
                className="text-sm cash-fund-description">{this.props.data.registryCategories[0].category.description}</p>
            </div>
            <button className="cash-fund-add-btn float-right mr-6" onClick={this.props.addButtonClickHandler}/>
          </div>
        </div>
      </div>
    )
  }
}

export default CashFundCardNotAdded;