import React, { useState } from 'react'
import "./GuestViewCard.scss";
import MoreOption from "../../commons/components/more-option/MoreOption";
import {
	sortableHandle,
} from 'react-sortable-hoc';
import { useSelector, useDispatch } from 'react-redux';
import Modal from "../../commons/components/modal-component/ModalComponent";
import CardItem from "../../commons/components/card-item/CardItem";
import { addProductHandler } from 'utils/commonFunction';
import Button from "../../commons/components/button-component/ButtonComponent";
import GiftStatusPill from "../../commons/components/gift-status-pill/GiftStatusPill";

const GuestViewCard = (props) => {

	const dispatch = useDispatch();
	const stateFilterApplied = useSelector(state => state.viewRegistry.viewFilterApplied.childFilter)

	// Handle drag
	// TODO: Change Draggable icon
	const DragHandle = sortableHandle(() => <i
		className={["ml-4 drag-icon m-a-popover m-a-popover-top-right", (props.disabledDrag || stateFilterApplied.length !== 0) ? "disabled-drag" : ""].join(" ")}>
		<div className="m-a-popover-content drag-drop-popover w-auto px-3 py-2">
			<p className="text-sm font-weight-normal m-a-popover-content-desc">Rearrange</p>
		</div>
	</i>);

	return (
		<>
			<div className="added-card py-5 pr-2">
				<div className="row pl-3">
					<div className="col-2 guest-card-image">
						{props.data?.item.imageUrl ?
							<img className="product-img d-block mx-auto" src={props.data?.item.imageUrl} alt={props.data?.title} />
							:
							<div className="added-card-no-product-image" />
						}
						{props.data.mostWanted ?
							<div
								className="d-flex align-items-center guest-must-have added-gift-must-have-btn px-1 added-gift-must-have-active">
								<i className="icon mr-1 d-block heart-fill" />
								<p className="text-sm added-gift-must-have-text">Must Have</p>
							</div>
							: <></>}
					</div>
					<div className="col">
						{/* <div className="text-sm category">{props.data.registryCategories[0].category.categoryName}</div> */}
						<h2>{props.data.title}</h2>
						<div className="d-flex mt-2">
							<div className="">${props.data.item.price}</div>
							{/*/!* TODO: Add Strikeout price dynamic *!/*/}
							{/*<div className="text-sm my-auto ml-1 strick-price">$257.99</div>*/}
							{props.data.quantity > 1 ?
								<div className="ml-4 text-body d-flex">
									Qty:{props.data.quantity}
									<div className="quantity-mul-text"> (${props.data.item.price} * {props.data.quantity})</div>
								</div> : <></>}
						</div>
						<div className="d-flex mt-3">
							{props.data.isPurchased ?
								<GiftStatusPill className="mr-1" variant="purchased" text="Purchased" />
								: null}
							{props.data.isReserved ?
								<GiftStatusPill className="mr-1" variant="reserved" text="Reserved" />
								: null}
							{props.data.isGroupGift ?
								<>
									<div class="guest-toggle-pills px-1 mr-3 d-flex align-items-center">
										<i class="guest-icon-active mr-1" />
										<span class="text-sm guest-icon-text">Group Gift</span>
									</div>	
									<div>
										<span className="text-sm font-medium received-amount">Received ${props.data.groupGift ? props.data.groupGift.amountReceived : 0} </span>
										<span className="text-sm total-amount">of ${props.data.item.price}</span>
										<div className="group-gift-background">
											<div className="group-gift-progress mw-100" style={{ width: `${props.data.groupGift ? props.data.groupGift.amountReceived / props.data.item.price * 100 : 0}%` }} />
										</div>
									</div>
								</>
								: null}
						</div>
					</div>
					<div className={[
						props.draggable ? "col-4" : "col-3",
					].join(" ")}
					>
						<div className="d-flex align-items-baseline justify-content-end mr-1">
							<Button variant="secondary" className="give-this-gift-btn font-medium text-body py-1 px-3 mr-3"
							// onClick={this.handleGuestView}
							>Give This Gift</Button>
						</div>
					</div>
				</div>
			</div>
			{/* TODO: change category */}
			<div className="row">
				<div className="col">
					{props.data.registryCategories.length > 1 ?
						props.data.registryCategories.map(category =>
							props.data.registryCategories[0].id !== category.id ?
								<div className="text-body font-medium p-6 added-card-other-category">
									You have added {props.data.item.quantity > 1 ? props.data.item.quantity : null} {category.category.categoryName} as part of <a className="title-link" tabIndex="true">{props.data.title}</a>
								</div>
								: null
						)
						: null}
				</div>
			</div>
		</>
	)
}

export default GuestViewCard